<template>
  <v-layout wrap>
    <v-flex xs3>
    <v-text-field multi-line v-model="text"></v-text-field>
    <v-btn @click="convert">convertir</v-btn>
  </v-flex>
  <v-flex xs9><pre>{{convertedText}}</pre></v-flex>
  </v-layout>
</template>

<script>
  export default {
    data () {
      return {
        text: '',
        convertedText: []
      }
    },
    methods: {
      convert () {
        const arr = this.text.split(/\r?\n/)
        for (let i = 0; i < arr.length; i += 4) {
          const transformDate = arr[i].split(',')
          const DMA = transformDate[0].split('.')
          const dia = DMA[0]
          const mes = DMA[1]
          const year = DMA[2]
          const fecha = `${mes}/${dia}/${year} ${transformDate[1]}`
          this.convertedText.push(
            {
              timestamp: new Date(fecha).getTime() / 1000,
              price: Number(arr[i + 3])
            }
            )
        }
        this.text = ''
      }
    }
  }
</script>



