import Vue from 'vue'
import Router from 'vue-router'
import home from '@/view/homeView'
import converter from '@/view/converter'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/converter',
      name: 'converter',
      component: converter
    }
  ]
})



